import React, { useRef } from 'react';

// references 2d canvas context
const canvasRef = useRef(null);
const canvasObject = canvasRef.current;
const ctx = canvasObject.getContext('2d')

export function draw(ctx, location) {
  console.log('drawing')
  ctx.fillstyle = 'hotpink';
  ctx.shadowColor = 'magenta';
  ctx.shadowBlur = 15;
  ctx.save();
  ctx.scale(SCALE, SCALE);
  ctx.translate(location.x / SCALE - OFFSET, location.y / SCALE - OFFSET);
  ctx.rotate(255 * Math.PI / 180);
  ctx.fill(SVG_PATH);
  ctx.restore();
};

// heart SVG path
const heartSVG = "M0 200 v-200 h200 a100,100 90 0,1 0,200 a100,100 90 0,1 -200,0 z"
const SVG_PATH = new Path2D(heartSVG);

// Scaling Constants for Canvas
const SCALE = 0.1;
const OFFSET = 80;
export const canvasWidth = window.innerWidth * .5;
export const canvasHeight = window.innerHeight * .5;


function App() {
  return (
    <div>Hello, Life!
      <canvas />
    </div>
  );
};

export default App;
